import { Component, OnInit, ViewChild } from '@angular/core';
import { DatatableComponent } from '@swimlane/ngx-datatable';
import { MeasurementAndDosageSizesService } from './../../services/MeasurementAndDosageSizesService';
import swal from 'sweetalert2';

@Component({
  selector: 'add-measurement',
  templateUrl: './add-measurement.component.html',
  styleUrls: [
    './add-measurement.component.scss',
    '../../../../assets/icon/icofont/css/icofont.scss'
  ],
  providers: [MeasurementAndDosageSizesService]
})
export class AddMeasurementComponent implements OnInit {
  rowsBasic = [];
  fullScreenRow = [];
  loadingIndicator = true;
  reorderable = true;
  packaging: any = {};
  measurement: any = [];
  packaging2: any = {};


  columns = [
    { prop: 'name' },
    { name: 'Gender' },
    { name: 'Company' }
  ];

  rows = [];
  expanded = {};
  timeout: any;


  rowsFilter = [];
  tempFilter = [];
  @ViewChild(DatatableComponent) table: DatatableComponent;

  measurements_array = [];

  constructor(public _addMeasurementServices: MeasurementAndDosageSizesService) {
    this.getMeasurements();
  }

  ngOnInit() {
  }

  getMeasurements() {
    this._addMeasurementServices.getMeasurements().subscribe(response => {
      if (response.code == 200) {
        this.measurements_array = response.data;
        this.tempFilter = this.measurements_array;
        this.rowsFilter = this.measurements_array;
      }
    });
  }// Get Measurements End
  public measurement_id = "";
  openMyModal(Id) {
    console.log(Id);
    var obj = this.measurements_array.find(x => x.Id == Id);
    this.measurement_id = Id;
    this.packaging2.name = obj.MeasurementName;
    this.packaging2.symbol = obj.MeasurementSymbol;
  }

  fetchBasicData(cb) {
    const req = new XMLHttpRequest();
    req.open('GET', 'assets/data/basic.json');

    req.onload = () => {
      cb(JSON.parse(req.response));
    };

    req.send();
  }

  fetchFullScreenData(cb) {
    const req = new XMLHttpRequest();
    req.open('GET', 'assets/data/fullscreen.json');

    req.onload = () => {
      cb(JSON.parse(req.response));
    };

    req.send();
  }

  fetchFilterData(cb) {
    const req = new XMLHttpRequest();
    req.open('GET', `assets/data/company.json`);

    req.onload = () => {
      cb(JSON.parse(req.response));
    };

    req.send();
  }

  updateFilter(event) {
    const val = event.target.value.toLowerCase();

    // filter our data
    const temp = this.tempFilter.filter(function (d) {
      return d.MeasurementName.toLowerCase().indexOf(val) !== -1 || !val;
    });

    // update the rows
    this.rowsFilter = temp;
    // Whenever the filter changes, always go back to the first page
    this.table.offset = 0;
  }

  onPage(event) {
    clearTimeout(this.timeout);
    this.timeout = setTimeout(() => {
      console.log('paged!', event);
    }, 100);
  }

  fetch(cb) {
    const req = new XMLHttpRequest();
    req.open('GET', `assets/data/100k.json`);

    req.onload = () => {
      const rows = JSON.parse(req.response);

      for (const row of rows) {
        row.height = Math.floor(Math.random() * 80) + 50;
      }

      cb(rows);
    };
    req.send();
  }

  getRowHeight(row) {
    return row.height;
  }
  onclick() {
    // alert("Hi!!");
  }
  //services
  addMeasurement = {};
  loadServices() {
    this._addMeasurementServices.addMeasurementService(this.packaging.name, this.packaging.symbol).subscribe(data => {
      this.addMeasurement = data;
      this.measurements_array.push(data.data);
      this.packaging = {};
    });
  }

  updateMeasurement() {
    console.log("Update");
    var index_number = this.measurements_array.findIndex(x => x.Id == this.measurement_id);
    this.measurements_array[index_number].MeasurementName = this.packaging2.name;
    this.measurements_array[index_number].MeasurementSymbol = this.packaging2.symbol;
    this._addMeasurementServices.updateMeasurement(this.measurement_id, this.packaging2.name, this.packaging2.symbol).subscribe(response => { });
  }

  //Delete Services
  deleteMeasurementServices(i) {
    // this.measurement.splice(i, 1);
    var self = this;
    swal({
      title: 'Are you sure?',
      text: 'You wont be able to revert',
      type: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Yes, delete it!'
    }).then(function (result) {
      if (result.value == true) {
        self._addMeasurementServices.deleteMeasurement(i).subscribe(response => {
          if (response.code == 200) {
            var index_number = self.measurements_array.findIndex(x => x.Id == i);
            self.measurements_array.splice(index_number, 1);
            self.rowsFilter = self.measurements_array;
            self.tempFilter = self.measurements_array;
            swal(
              'Deleted!',
              'Package has been deleted.',
              'success'
            );
          }
        });
      }
      else {
        // alert('No deletion');
      }
    }).catch(swal.noop);
  }
}



